<template class="bg-not-white">
  <div
    class="flex grid-cols-4 content-between font-display flex-wrap min-w-full"
  >
    <div class="flex-grow bg-empty-block min-h-full">
      <div class="text-center bg-white p-6">
        <span class="text-4xl font-bold text-black">form</span>
      </div>
    </div>
    <div class="min-h-full bg-form-color p-6 flex-grow">
      <form class="shadow-md rounded pt-6 pb-8 mb-4">
        <template v-if="errors.length">
          <b>Please fix this errors:</b>
          <ul>
            <li v-for="error in errors" :key="error">{{ error }}</li>
          </ul>
        </template>
        <span class="text-white font-body font-bold">Type X</span>
        <input
          v-model="formX"
          class="
            mb-4
            block
            w-full
            text-center
            shadow-md
            border-4 border-gray-300
            text-gray-900
            focus:outline-none focus:ring-gray-500 focus:border-gray-500
            font-display font-bold
            text-lg
            rounded-md
          "
        />
        <span class="text-white font-body font-bold">Type Y</span>
        <input
          v-model="formY"
          class="
            mb-4
            block
            w-full
            shadow-md
            text-center
            border-4 border-gray-300
            text-gray-900
            focus:outline-none focus:ring-gray-500 focus:border-gray-500
            font-display font-bold
            text-lg
            rounded-md
          "
        />
        <span class="text-white font-body font-bold">Type R</span>
        <input
          v-model="formR"
          class="
            mb-4
            block
            shadow-md
            w-full
            text-center
            border-4 border-gray-300
            text-gray-900
            focus:outline-none focus:ring-gray-500 focus:border-gray-500
            font-display font-bold
            text-lg
            rounded-md
          "
        />
        <button
          class="
            mr-8
            mb-1
            py-2
            px-4
            shadow-md
            border border-transparent
            shadow-sm
            text-sm
            leading-4
            font-display font-bold
            text-xl
            rounded-full
            text-white
            bg-gray-600
            hover:bg-gray-700
            transition-colors
            duration-300
            focus:outline-none
            focus:ring-2
            focus:ring-offset-2
            focus:ring-gray-500
          "
          type="button"
          @click.stop="submitForm()"
        >
          Submit
        </button>
        <button
          class="
            py-2
            px-4
            border border-transparent
            shadow-sm
            text-sm
            leading-4
            font-display font-bold
            text-xl
            rounded-full
            text-white
            bg-gray-600
            hover:bg-gray-700
            transition-colors
            duration-300
            focus:outline-none
            focus:ring-2
            focus:ring-offset-2
            focus:ring-gray-500
          "
          type="button"
          @click.stop="list = []"
        >
          Clear
        </button>
      </form>
    </div>
    <div
      class="flex-grow bg-table-color md:w-1/3"
      style="min-width: 150px"
      v-if="list.length > 0"
    >
      <table class="min-w-full divide-y divide-gray-200">
        <thead class="bg-gray-50">
          <tr>
            <th>X</th>
            <th>Y</th>
            <th>R</th>
            <th>Inside</th>
          </tr>
        </thead>
        <tbody class="bg-white divide-y divide-gray-200">
          <tr
            v-for="(point, idx) in list"
            :key="idx"
            class="text-center"
            :class="{ 'bg-empty-block': idx === list.length - 1 }"
          >
            <td>{{ point.x }}</td>
            <td>{{ point.y }}</td>
            <td>{{ point.r }}</td>
            <td>{{ point.inside }}</td>
          </tr>
        </tbody>
      </table>
    </div>
    <div class="min-h-full min-w-max">
      <SvgField :svg-size="svgSize" @reset="console.log(this.sizeX)">
        <template v-if="formR">
          <circle
            r="3"
            :cy="this.calcY(point.y)"
            :cx="calcX(point.x)"
            v-for="point in list"
            :key="point"
            :class="{
              'fill-empty-block': point.inside,
              'fill-black': !point.inside,
            }"
          />
        </template>
      </SvgField>
    </div>
  </div>
  <footer>
    <h1>Powered by Vue.js</h1>
  </footer>
</template>

<script>
import SvgField from "@/components/svg/SvgField";
export default {
  name: "Main",
  components: { SvgField },
  data() {
    return {
      formX: undefined,
      formY: undefined,
      formR: undefined,
      svgSize: 0,
      errors: [],
      list: [
        { x: 1, y: 1, r: 1, inside: true },
        { x: 2, y: 1, r: 1, inside: false },
        { x: 2, y: 4, r: 1, inside: true },
        { x: 3, y: 1, r: 1, inside: true },
        { x: 4, y: 1, r: 1, inside: true },
      ],
    };
  },

  props: {
    devMode: {
      type: Boolean,
      required: true,
      default: false,
    },
  },

  mounted() {
    this.$nextTick(() => {
      this.onResize();
      window.addEventListener("resize", this.onResize);
    });
  },

  beforeUnmount() {
    window.removeEventListener("resize", this.onResize);
  },

  methods: {
    validateByBorders(num, numName, a, b) {
      if (!num) this.errors.push(`Type ${numName}`);
      else {
        if (isNaN(num)) this.errors.push(`${numName} must be number`);
        else if (num > b || num < a) {
          this.errors.push(`${numName} should be in [${a}, ${b}]`);
        }
      }
    },
    submitForm() {
      this.errors = [];
      // Text (-5 ... 5) для координаты по оси X, Text (-3 ... 3) для координаты по оси Y, и Text (-5 ... 5) для задания радиуса области.
      this.validateByBorders(this.formX, "X", -5, 5);
      this.validateByBorders(this.formY, "Y", -3, 3);
      this.validateByBorders(this.formR, "R", -5, 5);
      if (this.errors.length === 0)
        this.list.push({
          x: this.formX,
          y: this.formY,
          r: this.formR,
          inside: this.isInside(),
        });
    },
    isInside() {
      return true;
    },

    calcX(x) {
      return ((x * 2) / this.formR) * this.svgR + this.svgHalf;
    },

    calcY(y) {
      return this.svgHalf - ((y * 2) / this.formR) * this.svgR;
    },

    onResize() {
      if (window.innerWidth > 1300) this.svgSize = 400;
      else {
        // if (window.innerWidth > 800) this.svgSize = 350;
        //else
        this.svgSize = 300;
      }
    },
  },
  computed: {
    svgR() {
      return this.svgSize / 6;
    },
    svgHalf() {
      return this.svgSize / 2;
    },
  },

  watch: {
    formX() {
      if (this.formX.includes(",")) {
        this.formX = this.formX.replace(",", ".");
      }
    },
    formY() {
      if (this.formY.includes(",")) {
        this.formY = this.formY.replace(",", ".");
      }
    },
    formR() {
      if (this.formR.includes(",")) {
        this.formR = this.formR.replace(",", ".");
      }
      if (!this.devMode) {
        if (this.formR > 5) {
          this.formR = this.formR[0];
        }
        if (this.formR < -5) {
          this.formR = this.formR[0];
        }
      }
    },
  },
};
</script>
